import React from "react";
import {
  AreaChart,
  Area,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  ResponsiveContainer,
} from "recharts";
import { getAssetsHistory } from "../../api/assets";
import { periods } from "./constants";
import Button from "react-bootstrap/Button";
import ButtonGroup from "react-bootstrap/ButtonGroup";
import { buildPeriod, parseTime, formatTick, sortPrice } from "./utils";
import { useDispatch } from "react-redux";
import { setErrorMessage } from "../../service/state";

function Chart({ coinData, periodParams, setPriceHL, priceHL }) {
  console.log("Chart");
  const [period, setPeriod] = React.useState(periods[0]);
  const [chartData, setChartData] = React.useState([]);
  const dispatch = useDispatch();

  React.useEffect(() => {
    if (periodParams) {
      const _period = periods.find(({ label }) => label === periodParams);
      if (_period) {
        setPeriod(_period);
      }
    }
  }, [periodParams]);

  React.useEffect(() => {
    const { start, end } = buildPeriod(period);
    getAssetsHistory(coinData.id, period.interval, start, end)
      .then((json) => {
        setChartData(
          json.data.map(({ time, ...rest }) => ({
            ...rest,
            date: parseTime(time, period.dateFormat),
          }))
        );

        const pricesHL = json.data.sort(sortPrice);

        setPriceHL({
          low: formatTick(pricesHL[0].priceUsd),
          high: formatTick(pricesHL[pricesHL.length - 1].priceUsd),
        });
      })
      .catch((error) => dispatch(setErrorMessage(error.message)));
  }, [coinData.id, period, setPriceHL, dispatch]);

  return (
    <>
      <ResponsiveContainer width="100%" height={500}>
        <AreaChart
          width={500}
          height={500}
          data={chartData}
          margin={{
            top: 10,
            right: 30,
            left: 0,
            bottom: 0,
          }}
        >
          <CartesianGrid strokeDasharray="3 3" />
          <XAxis dataKey="date" />
          <YAxis
            domain={[() => priceHL.low, () => priceHL.high]}
            orientation="right"
            mirror
            tickLine={false}
            tickFormatter={(value) => formatTick(value)}
          />
          <Tooltip />
          <Area
            type="monotone"
            dataKey="priceUsd"
            stroke="#8884d8"
            fill="#8884d8"
          />
        </AreaChart>
      </ResponsiveContainer>
      <ButtonGroup size="sm">
        {periods.map((_period) => (
          <Button
            onClick={() => setPeriod(_period)}
            key={_period.label}
            variant="outline-primary"
            active={_period.label === period.label}
          >
            {_period.label}
          </Button>
        ))}
      </ButtonGroup>
    </>
  );
}

export default Chart;
